import React, { Component } from 'react';
import './index.css';

export default class ToastComponent extends Component {
	state = {
		message: '',
		showToast: false,
	};
	componentDidMount() {
		window.toast = this.toast.bind(this);
	}
	toast(message = '操作成功~') {
		this.setState({ message, showToast: true });
		document.body.classList.add('lock-body');
	}
	close() {
		this.setState({ showToast: false });
		document.body.classList.remove('lock-body');
	}
	render() {
		let { message, showToast } = this.state;
		return (
			<div id="toast" className={`toast ${showToast ? 'toast--open' : ''}`}>
				<div className="toast__overlay"></div>
				<div className="toast__content">
					<h2>{message}</h2>
					<div className="footer">
						<div className="action" onClick={this.close.bind(this)}>
							确定
						</div>
					</div>
				</div>
			</div>
		);
	}
}
