import React, { Component } from 'react';
import './index.css';
export default class LoadingComponent extends Component {
	state = {
		show: true,
	};
	toggle() {
		this.setState({ show: !this.state.show });
	}
	render() {
		let { show } = this.state;
		return (
			show && (
				<div className="loading">
					<span></span>
					<span></span>
					<span></span>
					<span></span>
					<span></span>
					<span></span>
					<span></span>
				</div>
			)
		);
	}
}
