import React, { Component } from 'react';
import HeaderComponent from '../../components/pageHeader';
import { response } from '../../response';
import './home.css';
import LodingComponent from '../../components/loading';
export default class HomePage extends Component {
	loding;
	componentDidMount() {
		this.loding.toggle();
		document.addEventListener('visibilitychange', () => {
			if (document.hidden) {
				document.title = '你怎么走啦~';
			} else {
				document.title = "李子发的个人网站 - zifa.li's Personal Website";
			}
		});
	}
	jump(data) {
		let { history } = this.props;
		history.push(`/post/${data.article_id}`);
	}
	render() {
		return (
			<>
				<HeaderComponent />
				<LodingComponent
					ref={(child) => {
						this.loding = child;
					}}
				/>
				<section id="main" className="home-page">
					<div className="notice ">The Harder You Work , The Luckier You Will Be .</div>
					<div className="container ">
						<h1 className="label-title">聚焦</h1>
					</div>
					<div className="container swiper-container">
						<div className="swiper">
							<div className="item" onClick={this.jump.bind(this, response.data[0])}>
								<div className="mask">
									<span className="foverlay">开发中</span>
								</div>
								<img src={require('../../assets/feature1.jpg').default} alt="11" />
							</div>
							<div className="item" onClick={this.jump.bind(this, response.data[0])}>
								<div className="mask">
									<span className="foverlay">开发中</span>
								</div>
								<img src={require('../../assets/feature2.jpg').default} alt="11" />
							</div>
							<div className="item" onClick={this.jump.bind(this, response.data[0])}>
								<div className="mask">
									<span className="foverlay">开发中</span>
								</div>
								<img src={require('../../assets/feature3.jpg').default} alt="11" />
							</div>
							<div className="item wap" onClick={this.jump.bind(this, response.data[0])}>
								<div className="mask">
									<span className="foverlay">开发中</span>
								</div>
								<img src={require('../../assets/feature1.jpg').default} alt="11" />
							</div>
							<div className="item wap" onClick={this.jump.bind(this, response.data[0])}>
								<div className="mask">
									<span className="foverlay">开发中</span>
								</div>
								<img src={require('../../assets/feature2.jpg').default} alt="11" />
							</div>
						</div>
					</div>
					<div className="container">
						<h2 className="main-title">近况</h2>
						<div>
							{response.data.map((item, index) => {
								return (
									<div className="post-list" key={index}>
										<h1 className="entry-title" onClick={this.jump.bind(this, item)}>
											{item.title}
										</h1>
										<p>{item.rief_content}</p>
										<div className="infos">
											<i className="icon iconfont iconfont lphva-icon-fabu2"></i>
											2019年12月
											<i className="icon iconfont lphva-icon-gengduo1"></i>
										</div>
									</div>
								);
							})}
						</div>
					</div>
				</section>
			</>
		);
	}
}
