			html_content:
				'<h1><a id="_0"></a>纯函数定义</h1>\n<p>函数的返回结果只依赖于它的参数，并且在执行过程里面没有副作用</p>\n<p>例如：</p>\n<pre><code class="lang-">function foo(x, b) {\n  return x + y\n}\n</code></pre>\n<p>现在 foo 的返回结果只依赖于它的参数 x 和 b，foo(1, 2) 永远是 3。</p>\n<p>这就是纯函数的第一个条件：函数的返回结果只依赖于它的参数</p>\n<p>函数执行过程没有副作用<br />\n函数执行过程对产生了外部可观察的变化那么就说这个函数是有副作用的。</p>\n<pre><code class="lang-">const a = 1\nconst foo = (obj, b) =&gt; {\n  return obj.x + b\n}\nconst user = { age: 1 }\nfoo(user, 2) // =&gt; 3\nuser.age // =&gt; 1\n</code></pre>\n<p>我们把原来的 x 换成了 obj，我现在可以往里面传一个对象进行计算，计算的过程里面并不会对传入的对象进行修改，计算前后的 user 不会发生任何变化，计算前是 1，计算后也是 1，它现在是纯的。但是我再稍微修改一下它：</p>\n<pre><code class="lang-const">const foo = (obj, b) =&gt; {\n  obj.age = 28\n  return obj.x + b\n}\nconst user = { age: 1 }\nfoo(user, 2) // =&gt; 4\nuser.x // =&gt; 2\n</code></pre>\n<p>现在情况发生了变化，我在 foo 内部加了一句 obj.x = 2，计算前 user.x 是 1，但是计算以后 user.x 是 2。foo 函数的执行对外部的 user 产生了影响，它产生了副作用，因为它修改了外部传进来的对象，现在它是不纯的。</p>\n<p>但是你在函数内部构建的变量，然后进行数据的修改不是副作用：</p>\n<pre><code class="lang-">const foo = (b) =&gt; {\n  const obj = { x: 1 }\n  obj.x = 2\n  return obj.x + b\n}\n</code></pre>\n<p>虽然 foo 函数内部修改了 obj，但是 obj 是内部变量，外部程序根本观察不到，修改 obj 并不会产生外部可观察的变化，这个函数是没有副作用的，因此它是一个纯函数。</p>\n<hr />\n<pre><code class="lang-">function getDouble(num: number): number {\n  return num * 2\n}\n</code></pre>\n<p>getDouble 函数不是一个纯函数，因为它返回的结果并不等于输入的结果，我们在不知道 num 的值的情况下，并不能保证 getDouble(2) 的返回值是 3。虽然 getDouble 函数的代码实现并没有变化，传入的参数也没有变化，但它的返回值却是不可预料的，现在 getDouble(2) 是 3，可能过了一会就是 4 了，因为 a 可能发生了变化变成了 2。</p>\n<h1><a id="_59"></a>纯函数的好处</h1>\n<p>为什么要煞费苦心地构建纯函数？因为纯函数非常“靠谱”，执行一个纯函数你不用担心它会干什么坏事，它不会产生不可预料的行为，也不会对外部产生影响。不管何时何地，你给它什么它就会乖乖地吐出什么。如果你的应用程序大多数函数都是由纯函数组成，那么你的程序测试、调试起来会非常方便。</p>\n',
