			mark_content:
				'# 纯函数定义\n函数的返回结果只依赖于它的参数，并且在执行过程里面没有副作用\n\n \n例如：\n\n```\nfunction foo(x, b) {\n  return x + y\n}\n```\n`foo` 的返回结果只依赖于它的参数 x 和 b，foo(1, 2) 永远是 3。\n\n这就是纯函数的第一个条件：函数的返回结果只依赖于它的参数\n\n函数执行过程没有副作用\n函数执行过程对产生了外部可观察的变化那么就说这个函数是有副作用的。\n~~~\nconst a = 1\nconst foo = (obj, b) => {\n  return obj.x + b\n}\nconst user = { age: 1 }\nfoo(user, 2) // => 3\nuser.age // => 1\n~~~\n\n我们把原来的 x 换成了 obj，我现在可以往里面传一个对象进行计算，计算的过程里面并不会对传入的对象进行修改，计算前后的 user 不会发生任何变化，计算前是 1，计算后也是 1，它现在是纯的。但是我再稍微修改一下它：\n\n~~~const a = 1\nconst foo = (obj, b) => {\n  obj.age = 28\n  return obj.x + b\n}\nconst user = { age: 1 }\nfoo(user, 2) // => 4\nuser.x // => 2\n~~~\n\n现在情况发生了变化，我在 foo 内部加了一句 obj.x = 2，计算前 user.x 是 1，但是计算以后 user.x 是 2。foo 函数的执行对外部的 user 产生了影响，它产生了副作用，因为它修改了外部传进来的对象，现在它是不纯的。\n\n但是你在函数内部构建的变量，然后进行数据的修改不是副作用：\n~~~\nconst foo = (b) => {\n  const obj = { x: 1 }\n  obj.x = 2\n  return obj.x + b\n}\n~~~\n虽然 foo 函数内部修改了 obj，但是 obj 是内部变量，外部程序根本观察不到，修改 obj 并不会产生外部可观察的变化，这个函数是没有副作用的，因此它是一个纯函数。\n\n---\n```\nconst a = 1;\nfunction getDouble(num: number): number {\n  return num * 2\n}\n```\ngetDouble 函数不是一个纯函数，因为它返回的结果并不等于输入的结果，我们在不知道 num 的值的情况下，并不能保证 getDouble(2) 的返回值是 3。虽然 getDouble 函数的代码实现并没有变化，传入的参数也没有变化，但它的返回值却是不可预料的，现在 getDouble(2) 是 3，可能过了一会就是 4 了，因为 a 可能发生了变化变成了 2。',
