import React, { Component } from 'react';
import ClipboardJS from 'clipboard';
export default class FollowComponent extends Component {
	state = {
		infos: {
			nickname: '小书包',
			wechat: 'lizifa2013',
		},
	};
	onClick = () => {
		alert('正在努力创作~');
	};
	copy() {
		window.toast('微信号已复制~');
		let { infos } = this.state;
		let clipboard = new ClipboardJS('.btn', {
			text() {
				return infos.wechat;
			},
		});
		clipboard.on('success', () => {
			// alert('微信号已复制~');
			clipboard.destroy();
		});
		clipboard.on('error', () => {
			clipboard.destroy();
		});
	}
	render() {
		let { infos } = this.state;
		return (
			<div id="follow-me">
				<div className="flex-left">
					<div className="icon-box">
						<span className="icon iconfont lphva-icon-weixin"></span>
					</div>
					<div className="content">
						<div className="nickname">{infos.nickname}</div>
						<div className="wechat">{infos.wechat}</div>
					</div>
				</div>
				<div className="flex-right">
					<div className="add_button btn" onClick={this.copy.bind(this)}>
						加我
					</div>
				</div>
			</div>
		);
	}
}
